<template>
  <div class="musiclist">
  <div class="music-list" v-for="(music,index) in musicData" :class="{active:index===isActive}">
  	<img :src="music.musicImgSrc" class="music-img" v-lazy="music.musicImgSrc">
  	<span class="music-index">{{index+1}}</span>
  	<span class="music-name" id="musicName" @click="changeMusic(index)" @mousedown="isActive=index" @mouseup="isActive=null" @touchstart="isActive=index" @touchend="isActive=null" @touchmove="isActive=null">{{music.name}}</span>
    <span class="del" @click="del(index)"><i class="del-icon"></i></span>
  </div>
  </div>
</template>

<script>
export default {
  name: 'musiclist',
  data () {
    return {
      continueClick:null,
      isActive:''
    }
  },
  created(){
    //
    if(this.musicData==""){
      this.$store.dispatch('musicList');}
  },
  computed:{
  	musicData(){
  		return this.$store.state.musicData;
  	},
    audio(){
      return this.$store.state.audio;
    }
  },
  watch:{
    musicData:{
      handler(val,oldval){
        localStorage.music = JSON.stringify(val);
      },
      deep:true
    }
  },
  mounted() {
    var musicName=document.getElementById('musicName');
    musicName.addEventListener('touchstart',function(ev){
      var e=ev||window.event;
      // e.preventDefault();
    },{passive:true})
    musicName.addEventListener('touchmove',function(ev){
      var e=ev||window.event;
      // e.preventDefault();
    },{passive:true})
    musicName.addEventListener('touchend',function(ev){
      // var e=ev||window.event;
      // e.preventDefault();
    })
},
methods:{
	changeMusic(index){
    var _this=this;
    var config={
        method: 'post',
            url: '/api/play-music',
            data: {
            musicID:this.musicData[index].id
            }
    }
    this.$store.commit('newaudio',{index:index,src:''});

    // 2s
    if(this.continueClick!=null){
        clearTimeout(this.continueClick);
      }
      this.continueClick=setTimeout(()=>{

        // 
    this.axios(config).then(function(response){
      if(_this.audio.index===index){// 2s
        var src=response.data.data[0].url;
        _this.$store.commit('newaudio',{index:index,src:src});
        _this.$store.commit('play',true);
        _this.$store.commit('playImgSrc','stopImgSrc');
      }
      }).catch(function(response){
        console.log(response)
      });
      },2000)
	},

  // 
  del(index){
    if(this.audio.index===index){//
      this.next();
    }
    this.$store.commit('delmusicData',index);
  },
  next(){
    var index=this.audio.index==this.musicData.length-1?0:this.audio.index+1;
    var _this=this;
    var config={
        method: 'post',
        url: '/api/play-music',
        data: {
        musicID:this.musicData[index].id
        }
    }
    this.$store.commit('newaudio',{index:index,src:''});
    this.axios(config).then(function(response){
      if(_this.audio.index===index){
        var src=response.data.data[0].url;
        _this.$store.commit('newaudio',{index:index,src:src});
      }
      }).catch(function(response){
        console.log(response)
      });
  }
}
}
</script>

<style lang="scss" scoped>
.musiclist{
	flex:8;
	overflow: auto;
  .music-list.active{
    background-color: #e6e6e6;
  }
	.music-list{
		width:100%;
		height:70px;
		border-bottom: 1px solid rgba(0, 0, 0, 0.05);
         display:flex;
		.music-img,img[lazy=loading]{
	        width:60px;
	        height:60px;
	        margin:5px 10px;
        }
        img[lazy=loading]{
          background-color: #e6e6e6;
        }
        span{
        	height:70px;
        	display: inline-block;
        	// vertical-align: top;
          line-height: 70px;
        }
        .music-index{
        	width:30px;
          text-align: center;
        }
        .music-name{
          flex:7;
          overflow:hidden;
          padding-left: 10px;
          padding-right:10px;
          font-size: 18px;
        	cursor:pointer;
        	-webkit-tap-highlight-color: rgba(0,0,0,0);
            -webkit-tap-highlight-color: transparent;
        }
        .del{
          height:70px;
          flex:1;
          display:flex;
          align-items:center;
          i{
            width:20px;
            height:20px;
            // margin-top:25px;
            display:inline-block;
            cursor:pointer;
            background:url('../assets/icon/del.png') no-repeat center center;background-size: contain;
          }
        }
	}
}
</style>


